<template>
  <div class="agree-wrap" style="margin-top:30px; margin-bottom:30px;" avalonctrl="vmParameter">
    <h2 class="title">服务协议</h2>
    <div v-html="msg"></div>
  </div>
</template>

<script>
import Axios from "@/common/js/Axios";

export default {
  mounted() {
    this.getXy();
  },
  data() {
    return {
      token: localStorage.getItem("Authorization"),
      msg:""
    };
  },
  methods: {
    getXy() {
      Axios.getAxios(
        "/api/payagreement/",
        {},
        {
          Authorization: "Bearer " + this.token
        }
      ).then(
        res => {
          this.msg = res.data.msg.announcement
        },
        err => {            
            this.$message.error("请登陆后重试")
        }
      );
    }
  }
};
</script>

<style lang="stylus" scoped>
.title {
  text-align: center;
}
.agree-wrap > p {
  color: #666666;
  font-size: 12px;
}
.agree-wrap h2 {
  padding: 20px 0 10px;
  display: block;
  // font-size: 1.17em;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.agree-wrap {
  width: 900px;
  padding: 0 50px 50px;
  margin: 0 auto;
  border: 1px solid #DBDBDB;
  background-color: #FFFFFF;
  margin-top: 30px;
  margin-bottom: 30px;
}
</style>



