<template>
  <div>
    <div :style="'background:url('+footerBg+')no-repeat'" class="footer">
      <div class="footer-center">
        <ul class="footer-list clear">
          <router-link to="/" tag="li" style="color:#fff">首页</router-link>
          <router-link to="/cl/recommend" tag="li" style="color:#fff">创建策略</router-link>
          <router-link to="/compangMsg" tag="li" style="color:#fff">关于我们</router-link>
          <router-link to="/newplayer" tag="li" style="color:#fff">新手指导</router-link>
          <router-link to="/me" tag="li" style="color:#fff">个人中心</router-link>
        </ul>
        <div class="footer-wrap clear">
          <div class="footer-left fl">
            <span>
              <i class="iconfont">
              &#xe666;
                客服QQ：{{msg.qq}}
              </i>
            </span>
            <span>
              <i class="iconfont">
                &#xe61a;
                邮箱地址：{{msg.email}}
              </i>
            </span>
            <span>
              <i class="iconfont">
                &#xe60c;
                公司地址：{{msg.address}}
              </i>
            </span>
          </div>
          <!-- <div class="footer-right fr">
            <img :src="Android" style="width: 120px; height: 120px;">
          </div> -->
        </div>

        <div class="footer-bottom">
          <span class="fl">
            <img :src="bgRenz">
          </span>
          <span class="fr">客户端下载：
            <a href="#">Android
              <span>
                <img :src="Ios" width="100%" height="100%">
              </span>
            </a>
            <a href="#">IOS
              <span>
                <img :src="Ios" width="100%" height="100%">
              </span>
            </a>
            <!-- <a v-if="isExe" download="app.exe" :href="isExe">windows版 -->
              <!-- <span>
                <img :src="WebCode" width="100%" height="100%">
              </span> -->
            <!-- </a> -->
          </span>
        </div>
      </div>
    </div>
    <div style="height:auto" class="footer-icp">
      <p>{{msg.record_number}}        
      </p>
      <p style="line-height:1.6;padding-bottom:20px;text-align:left;padding:0 50px">
        风险揭示：投资有风险，当您进行投资时，可能获得投资收益，但同时也面临着投资风险，比如资金损失风险、运营风险、流动性风险等。您在做出投资决策之前，请仔细阅读相关风险揭示书和投资协议、公司章程或者合伙协议 等文件，充分认识投资的风险收益特征和产品特性，认真考虑可能存在的各项风险因素，并充分考虑自身的风险承受能力，理性判断并谨慎做出投资决策。
      </p>
    </div>
  </div>
</template>

<script>
import Axios from "@/common/js/Axios";
export default {

  mounted(){
    this.getMsg();
    this.getPic();
  },
  methods: {
    getPic(){
      Axios.getAxios("/api/apps", {
      }).then(
        res => {          
          this.Ios = this.WebCode = this.Android = res.data.app_qrcode;
          this.isExe = res.data.exe_url;
        },
        err => {
          console.log(err);
        }
      );
    }
    ,
    getMsg() {
      Axios.getAxios("/api/contactWay", {
      }).then(
        res => {
          this.msg = res.data.data;
        },
        err => {
          console.log(err);
        }
      );
    }
  },
  data() {
    return {
      msg:{},
      isExe:false,
      //图片部分开始
      loginImg: require("@/assets/login/images/logo-login.png"),
      Android: "",
      Ios: "",
      WebCode: "",
      bgRenz: require("@/assets/public/images/bg-renz.png"),
      footerBg: require("@/assets/public/images/footer-bg.png")
      //图片部分结束
    };
  }
};
</script>

<style lang="stylus" scoped>
/* 底部 */
.footer {
  width: 100%;
  min-width: 1200px;
  height: 435px;
  background-size: 100% 100%;
}
.footer-center {
  width: 1005px;
  margin: auto;
}
.footer-center .footer-list {
  padding: 25px 0 90px 0;
  width: 700px;
  margin: auto;
}
.footer-center .footer-list li {
  float: left;
  margin: 0 30px;
  cursor: pointer;
}
.footer-center .footer-list li a {
  color: #fff;
}
.footer-wrap .footer-left span {
  display: block;
  margin-bottom: 15px;
  color: #88919e;
  font-size: 15px;
}
.footer-wrap .footer-left span i {
  display: inline-block;
  vertical-align: middle;
}
.footer-wrap .footer-left span:hover {
  color: #fff;
  cursor: pointer;
}
.footer-bottom {
  margin-top: 30px;
}
/* .footer-bottom span { line-height: 40px; color: #fff; }
.footer-bottom span a { display: inline-block; width: 105px; height: 105px; line-height: 25px; border: 1px solid #88919e; border-radius: 3px; text-align: center; color: #fff; margin-left: 40px; } */
.footer-icp {
  width: 100%;
  height: 60px;
  line-height: 60px;
  background: #2f3542;
  color: #88919E;
  text-align: center;
}
.footer-bottom {
  float: right;
  margin-top: 55px;
  margin-left: 40px;
  color: #dfdfdf;
  width: 1000px;
}
.footer-bottom span a {
  color: #fff;
  display: inline-block;
  width: 100px;
  height: 25px;
  text-align: center;
  line-height: 25px;
  border-radius: 20px;
  border: 1px solid #dfdfdf;
  margin-top: 10px;
  margin-right: 20px;
  position: relative;
}
.footer-bottom span a:hover {
  background: #fff;
  color: #000;
}
.footer-bottom span a:hover span {
  display: block;
}
.footer-bottom span a span {
  display: block;
  width: 100px;
  height: 100px;
  position: absolute;
  top: -110px;
  left: 0;
  // display: none;
}
</style>



