<template>
    <div class="container" style="min-height:450px;background:#f5f4f4;">
        <div class="main ">
            <div class="box">
                <div class="clearfix">
                    <div class="uc-l">
                        <user-msg></user-msg>
                        <user-router></user-router>
                    </div>
                    <div class="uc-r">
                        <router-view/>
                    </div>
                </div>                
            </div>
        </div>
    </div>
</template>

<script>
import UserMsg from './components/UserMsg'
import UserRouter from './components/UserRouter'
export default {
    components:{
        UserMsg,
        UserRouter
    }
}
</script>

<style lang="stylus">
@import '~@/assets/me/me'
</style>

<style lang="stylus" scoped>
/*全局样式*/
.box{ width:1200px; margin:0 auto;}
.container{
    padding-top :60px;
    padding-bottom 60px;
}
</style>




