<template>
  <div class="out">
    <p class="title"><i></i>企业对公账号</p>
        <!-- <h1 class="left" style="text-indext:2em">&nbsp;&nbsp;入金维护中</h1> -->
    <div class="box">
      <p class="nmb">请输入充值金额</p>
      <div class="sb">
        <el-input v-model="sms" placeholder="请输入充值金额"></el-input>
          <!-- <el-button @click="pay" type="danger" style="text-align:center;margin:40px auto;width:200px;display:block">提交</el-button> -->
      </div>
      <div class="sb" style="margin-top:30px;">
        <el-input v-model="sms2" placeholder="请输入银行卡号"></el-input>
          <el-button @click="pay" type="danger" style="text-align:center;margin:40px auto;width:200px;display:block">提交</el-button>
      </div>
      
      <p class="ts">周一至周日（7*24小时实时入金）</p>
    </div>
  </div>
</template>

<script>
import Axios from "@/common/js/Axios";

export default {
  data(){
    return{
      img:require("@/assets/ewm.d572221.jpg"),
      sms:"",
      sms2:""
    }
  },
  methods: {
    pay() {
      let userToken = localStorage.getItem( "Authorization");
      Axios.postAxios(
        "/api/threepay",
        {
          money: this.sms,
          card:this.sms2
        },
        {
          Authorization: "Bearer " + userToken
        }
      ).then(res => {
          if(res.data.code != '1'){
            this.$message.error('充值失败');
          }else{
            window.location.href = res.data.url;          
          }
      },err=>{
        this.$message.error("充值失败")
      });
    }
  }
};
</script>

<style lang="stylus" scoped>
h2{
  font-size: 18px;
    font-weight: 800;
    color: #c5965a;
    margin: 10px auto;
}
h1{
      font-weight: bold;
    font-size: 40px;
    color: red;
}
.title{
    color: #c5965a;
    font-size: 15px;
    font-weight: bold;
    border-bottom: 1px solid #e6e6e6;
    padding-bottom: 5px;
    padding-left: 10px;
    margin: 0 10px;
    line-height 40px;
    i{
            display: inline-block;
        width: 10px;
        height: 10px;
        background: #a77335;
        border-radius: 100%;
        margin-right: 15px;
    }
}
.nmb{
      line-height: 100px;
    text-align: center;
    font-size: 30px;
}
.ts{
  text-align: center;
    font-size: 20px;
    color: red;
}
.box {
  // width: 400px;
    margin-left 50px;
  // margin: auto;
  // margin-top: 50px;
  width 600px
  .item {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    height: 40px;
    .left, .right {
      flex: 1;
      font-size: 14px;
      color: #000;
      .ui-input {
        border: 1px solid #C5C5C5;
        padding: 2px 5px;
        width: 155px;
        font-size: 14px;
        line-height: 28px;
        height: 28px;
        border-radius: 2px;
        min-width: 50px;
        border-radius: 2px;
        padding: 0 0 0 15px;
        border: 1px solid #C5C5C5;
        resize: none;
        vertical-align: middle;
      }
    }
    .right {
      flex: 3;
      span {
        font-weight: 700;
        color: #FF6600;
      }
    }
  }
  .submit {
    display: block;
    width: 235px;
    height: 35px;
    text-align: center;
    color: #fff;
    border: none;
    background-color: red;
    border-radius: 4px;
    font-size: 14px;
    line-height: 35px;
    margin-top: 30px;
    font-weight: 400;
  }
}
</style>




