          <div class="platform-wrap">
            <ul class="platform-wrap-list" id="about_news">
              <!-- <li :style="'background:url('+notice+') top left no-repeat'">
                <div>
                  <p onclick="showContent(42)">媒体资讯10<small>发表于2018-11-07 00:00:00</small></p>
                </div>
              </li>
              <li></li>               -->
              <iframe frameborder="0" width="1200" height="630" scrolling="yes" src="https://www.jin10.com/example/jin10.com.html?messageNum=50&fontSize=14px&theme=white"></iframe>
            </ul>
            <!-- <div id="page" class="page_div"><a id="firstPage">首页</a><a id="prePage">上一页</a><a class="current">1</a><a>2</a><a id="nextPage">下一页</a><a id="lastPage">尾页</a></div> -->
          </div>
        </div>
      </div>
