<script>
export default {
  data() {
    return {
      //图片开始
      notice : require('@/assets/forme/images/notice.png')
      //图片结束
    };
  },
  components: {}
};
</script>

<style lang="stylus" scoped>

	.page_div {
                margin:0 auto;
				margin-top: 20px;
				margin-bottom: 20px;
				font-size: 15px;
				font-family: "microsoft yahei";
				color: #666666;
				margin-right: 10px;
				padding-left: 20px;
				box-sizing: border-box;
                width:560px;    
                text-align:center;           
			}
			/*
			 * é¡µæ•°æŒ‰é’®æ ·å¼
			 */
			.page_div a {
				min-width: 30px;
				height: 28px;
				border: 1px solid #dce0e0!important;
				text-align: center;
				margin: 0 4px;
				cursor: pointer;
				line-height: 28px;
				color: #666666;
				font-size: 13px;
				display: inline-block;
                text-decoration:none;
			}
			#firstPage,
			#lastPage {
				width: 50px;
				color: #f00;
