<template>
    <div>
        <!-- banner -->
        <div class="contianer-banner">
			<img :src="banner">
		</div>
        <div>
            <top-nav @tabNav="tabNav"></top-nav>
            <div id="pageTab">
                <msg v-show="tabIndex == 0"></msg>
                <call-me v-show="tabIndex == 1"></call-me>
                <news v-show="tabIndex == 2"></news>
                <notice v-show="tabIndex == 3"></notice>
            </div>
        </div>
    </div>
</template>

<script>
import TopNav from './components/TopNav'
import Msg from './components/Msg'
import CallMe from './components/CallMe'
import News from './components/News'
import Notice from './components/Notice'
export default {
    data(){
        return{
            tabIndex:0,
            //图片开始
            banner : require('@/assets/forme/images/br1.png')
            //图片结束
        }
    },
    components : {
        TopNav,
        Msg,
        CallMe,
        News,
        Notice
    },
    methods:{
        tabNav(index){
            this.tabIndex = index;
            console.log(this.tabIndex);
        }
    }
}
</script>

<style lang="stylus" >
@import '~@/assets/forme/css/compangMsg'
</style>




