<template>
  <span>
    <div class="company-container">
      <div class="company-introduction">
        <div class="meassge-center">
          <ul class="meassge-list clear">
            <li>
              <span class="fl"><img :src="us1"></span>
              <div class="fl">
                <p>客服QQ：<span class="color6">{{msg.qq}}</span></p>
                <!-- <small class="color9">在线时间：9:00-18:00（工作日）</small> -->
              </div>
            </li>

            <li>
              <span class="fl"><img :src="us3"></span>
              <div class="fl">
                <p>邮箱地址：<span class="color6">{{msg.email}}</span></p>
              </div>
            </li>
            <li>
              <span class="fl"><img :src="us3"></span>
              <div class="fl">
                <p>公司地址</p>
                <i>{{msg.address}}</i>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </span>
</template>

<script>
import Axios from "@/common/js/Axios";

export default {
  mounted(){
    this.getMsg();  
  },
  data() {
    return {
      //图片开始
      us1: require("@/assets/forme/images/us1.png"),
      us3: require("@/assets/forme/images/us3.png"),
      msg:{}
      //图片结束
    };
  },
  methods:{
    getMsg() {
      Axios.getAxios("/api/contactWay", {
      }).then(
        res => {
          this.msg = res.data.data;
        },
        err => {
          console.log(err);
        }
      );
    }
  },
  components: {}
};
</script>

<style lang="stylus" scoped>
.meassge-center {
  width: 1100px;
  margin: 45px 0 auto 100px;
}
.meassge-center .meassge-list li { /* Safari */
  float: left;
  width: 50%;
  height: 105px;
  line-height: 25px;
  vertical-align: middle;
  overflow: hidden;
  display: -webkit-flex; /* Safari */
  -webkit-align-items: center; /* Safari 7.0+ */
  display: flex;
  align-items: center;
}
.meassge-center .meassge-list li > span {
  margin-right: 10px;
}
.meassge-center .meassge-list li p {
  font-size: 20px;
  color: #000;
  vertical-align: middle;
  flex: 1;
}
.meassge-center .meassge-list li b {
  font-size: 15px;
  color: #000;
}
</style>




