                    <!-- 四个切换 -->
                    <div class="tab-cons">
                        <router-view />
                    </div>
                </div>  
            </div>
        </div>
    </div>    
</template>

<script>
//顶部切换
import TabTitle from './components/TabTitle'

export default {
    components :{
        TabTitle,
    }
}
</script>

<style lang="stylus">
@import '~@/assets/cl/css/cl'
.trade-page .trade-stock .stock-line .stock-chart {
    width: 100%;
    height: 255px;
    overflow: hidden;
}
.trade-page .stock-search1{
    padding-left 106px;
}
.trade-page .stock-search1 .search-result{
    margin-left 0
}
</style>



