<template>
    <div class="header">
        <div class="login-center">
			<router-link tag="a" to="/" class="fl">
				<img src="/uploads/images/logo/logo_pc.png">
            </router-link>
			<span class="fr">
				<router-link active-class="active" tag="a" to="/login/register">注册</router-link>
				<router-link active-class="active" tag="a" to="/login/login">登录</router-link>
			</span>
		</div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            //图片部分开始
            loginImg:require('@/assets/login/images/logo-login.png')
            //图片部分结束
        }
    }
}
</script>

<style lang="stylus" scoped>
.header{
    width: 100%;
    min-width: 1200px;
    height: 110px;
    border-bottom: 2px solid #dedede;
    .login-center {
        width: 1200px;
        padding: 8px;
        margin: 0 auto 0 auto;
        overflow: hidden;
        padding-top: 30px;
        span{
            a{
                width: 86px;
                height: 35px;
                line-height: 35px;
                text-align: center;
                margin-top: 10px;
                float: left;
                color: #666;
                font-size: 15px;
                border: 1px solid #dbdbe0;
            }
            .active{
                background: #e25453;
                color: #fff;
                border-color: #e25453;
            }
        }
    }
}
</style>



