<template>
    <div>
        <div :style="'background:url('+bg+')no-repeat center'" class="banner"></div>
        <div class="container">
            <img :src="img2" alt="">
            <img :src="img3" alt="">
            <img :src="img4" alt="">
            <img :src="img5" alt="">
            <img :src="img6" alt="">
            <img :src="img7" alt="">
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            //图片开始
            bg : require("@/assets/newplayer/img_01.jpg"),
            img2 : require("@/assets/newplayer/img_02.jpg"),
            img3 : require("@/assets/newplayer/img_03.jpg"),
            img4 : require("@/assets/newplayer/img_04.jpg"),
            img5 : require("@/assets/newplayer/img_05.jpg"),
            img6 : require("@/assets/newplayer/img_06.jpg"),
            img7 : require("@/assets/newplayer/img_07.jpg"),
            //图片结束
        }
    }
}
</script>

<style lang="stylus" scoped>
.banner{
    height 260px;
    width 100%;
}
.container{
    width 1442px;
    margin 0 auto;
}
</style>



